<template>
  <div
    class="co-artboard"
    v-loading="loading"
    :element-loading-text="loadingText"
    element-loading-spinner="el-icon-loading"
  >
    <div class="co-artboard-main">
      <i class="p-r-t el-icon-close" @click="hangup"></i>
      <div class="drawingarea">
        <div class="select-list">
          <div v-for="v in handleList" :key="v.type" class="select-item">
            <el-color-picker
              v-model="color"
              show-alpha
              v-if="v.type === 'color'"
              @change="colorChange"
              :disabled="!isToPeer"
            ></el-color-picker>
            <button
              :disabled="v.type === 'cancel' ? 
              !isToPeer || allowCancel : v.type === 'go' ? !isToPeer || allowGo
              :!isToPeer"
              @click="handleClick(v)"
              v-if="!['color', 'lineWidth', 'polygon'].includes(v.type)"
              :class="{active: currHandle === v.type}"
            >{{v.name}}</button>
            <el-popover placement="top" width="400" trigger="click" v-if="v.type === 'polygon'">
              <el-input-number
                v-model="sides"
                controls-position="right"
                @change="sidesChange"
                :min="3"
                :max="10"
              ></el-input-number>
              <button
                slot="reference"
                :disabled="!isToPeer"
                @click="handleClick(v)"
                :class="{active: currHandle === v.type}"
              >{{v.name}}</button>
            </el-popover>
            <el-popover placement="top" width="400" trigger="click" v-if="v.type === 'lineWidth'">
              <el-slider v-model="lineWidth" :max="20" @change="lineWidthChange"></el-slider>
              <button slot="reference" :disabled="!isToPeer">
                {{v.name}}
                <i>{{lineWidth + 'px'}}</i>
              </button>
            </el-popover>
          </div>
          <div>
          <a href="" download="test" id="download"></a>
          </div>
        </div>
        <div class="draw">
          <canvas width="900" height="500" ref="canvas"></canvas>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Palette from "@/utils/artboard"
import { coArtBoardHandleOption } from "@/const"
const offerOption = {
  offerToReceiveAudio: 1,
  offerToReceiveVideo: 1
}

export default {
  name: "ArtBoardPage",
  props: ["currentconversation", "state", "webRtcType"],
  data() {
    return {
      conversation: {},
      loading: false,
      loadingText: "...",
      isToPeer: false, //  P2P 
      peer: null,
      handleList: coArtBoardHandleOption,
      // 
      color: "rgb(0, 0, 0)",
      currHandle: "line",
      lineWidth: 5,
      palette: null, // 
      allowCancel: true, // undo
      allowGo: true, // redo
      sides: 3, // 
      channel: null,
      messageList: []
    }
  },
  methods: {
    send(arr) {
      // 
      console.log("send", arr)
      this.channel.send(JSON.stringify(arr))
    },
    initPalette() {
      this.palette = new Palette(this.$refs["canvas"], {
        drawColor: this.color,
        drawType: this.currHandle,
        lineWidth: this.lineWidth,
        allowCallback: this.allowCallback,
        moveCallback: this.moveCallback
      })
    },
    moveCallback(...arr) {
      // 
      this.send(arr)
    },
    allowCallback(cancel, go) {
      this.allowCancel = !cancel
      this.allowGo = !go
    },
    sidesChange() {
      // 
      this.palette.changeWay({ sides: this.sides })
    },
    colorChange() {
      // 
      this.palette.changeWay({ color: this.color })
    },
    lineWidthChange() {
      // 
      this.palette.changeWay({ lineWidth: this.lineWidth })
    },
    handleClick(v) {
      // 
      if (["cancel", "go", "clear"].includes(v.type)) {
        this.moveCallback(v.type)
        this.palette[v.type]()
        return
      }
      this.palette.changeWay({ type: v.type })
      if (["color", "lineWidth"].includes(v.type)) return
      this.currHandle = v.type
    },
    hangup() {
      // 
      this.$message({message: "~", type: "warning"})
      this.$socket.emit("1v1hangup", {...this.conversation, webRtcType: this.webRtcType})
      this.clearState()
      this.$store.dispatch('app/SET_ISTOCOARTBOARD', false)
      this.$eventBus.$emit('hangup') // webRTCStateapply
    },
    clearState() {
      // 
      this.peer.close()
      this.channel.close()
      this.palette.destroy()
      this.peer = null
      this.palette = null
      this.channel = null
      this.isToPeer = false
    },
    apply() {
      this.loading = true
      this.loadingText = "..."
      const data = {...this.conversation, webRtcType: this.webRtcType}
      this.$socket.emit("apply", data)
    },
    reply(type) {
      console.log('emit socket reply')
      this.$socket.emit("reply", {...this.conversation, webRtcType: this.webRtcType})
    },
    async createP2P(data) {
      console.log('P2P',this.state)
      this.loading = true
      this.loadingText = ""
      await this.initPeer(data) //  RTCPeerConnection
    },
    createDataChannel() {
      //  DataChannel
      console.log('DataChannel')
      try {
        this.channel = this.peer.createDataChannel("messagechannel")
        this.handleChannel(this.channel)
      } catch (e) {
        console.log("createDataChannel:", e)
      }
    },
    onDataChannel() {
      //  DataChannel
      console.log('DataChannel', this.state)
      this.peer.ondatachannel = event => {
        console.log('ondatachannel', event)
        this.channel = event.channel
        this.handleChannel(this.channel)
      }
    },
    handleChannel(channel) {
      //  channel
      channel.binaryType = "arraybuffer"
      channel.onopen = event => {
        // 
        console.log("channel onopen", event)
        this.isToPeer = true // 
        this.loading = false
        this.initPalette()
      }
      channel.onclose = function(event) {
        // 
        console.log("channel onclose", event)
      }
      channel.onmessage = e => {
        // 
        console.log('', e.data)
        if (Array.isArray(JSON.parse(e.data))) {
          let [type, ...arr] = JSON.parse(e.data)
          // console.log('onmessage', type, arr)
          this.palette[type](...arr)
        } else {
          this.messageList.push(JSON.parse(e.data))
        }
        // console.log('channel onmessage', e.data)
      }
    },
    initPeer(data) {
      //  PeerConnection
      console.log('peer')
      let PeerConnection =
        window.RTCPeerConnection ||
        window.mozRTCPeerConnection ||
        window.webkitRTCPeerConnection
      this.peer = new PeerConnection()
      // ICE 
      this.peer.onicecandidate = event => {
        console.log('ICEcandidate1V1ICE')
        if (event.candidate) {
          this.$socket.emit("1v1ICE", {
            ...data,
            sdp: event.candidate
          })
        }
      }
    },
    async createOffer(data) {
      // DataChannel offer
      try {
        // offer
        console.log('offer')
        let offer = await this.peer.createOffer(this.offerOption)
        console.log('offer')
        //  offer 
        console.log('setLocalDesc')
        await this.peer.setLocalDescription(offer)
        console.log('setLocalDesc')
        //  offer
        this.$socket.emit("1v1offer", {
          ...data,
          sdp: offer
        })
      } catch (e) {
        console.log("createOffer: ", e)
      }
    },
    async onOffer(data) {
      // offer answer
      try {
        //  offer 
        console.log('offer')
        await this.peer.setRemoteDescription(data.sdp) // this.peer.onicecandidateICE
        console.log('offer')
        //  answer
        console.log('answer')
        let answer = await this.peer.createAnswer()
        console.log('answer')
        //  answer 
        console.log('answer')
        await this.peer.setLocalDescription(answer)
        console.log('answer')
        //  answer
        this.$socket.emit("1v1answer", {
          ...data,
          sdp: answer
        })
      } catch (e) {
        console.log("onOffer: ", e)
      }
    },
    async onAnswer(data) {
      // answer
      try {
        console.log('answer')
        await this.peer.setRemoteDescription(data.sdp) //  answer 
        console.log('answer')
      } catch (e) {
        console.log("onAnswer: ", e)
      }
    },
    async onIce(data) {
      //  ICE 
      try {
        console.log('ICE')
        await this.peer.addIceCandidate(data.sdp) //  ICE
        console.log('ICE')
      } catch (e) {
        console.log("onAnswer: ", e)
      }
    },
    handlerResize() {
      const drawArea = document.querySelector('.draw')
      const H = getComputedStyle(drawArea).height.replace('px', '')
      const W = getComputedStyle(drawArea).width.replace('px', '')
      this.$refs['canvas'].width = W
      this.$refs['canvas'].height = H
    }
  },
  sockets: {
    async reply(data) {
      // 
      this.loading = false
      // console.log(data)
      //  peer
      await this.createP2P(data)
      // DataChannel
      await this.createDataChannel()
      //  offer
      this.createOffer(data)
    },
    "1v1answer"(data) {
      //  answer
      this.onAnswer(data)
    },
    "1v1ICE"(data) {
      //  ICE
      this.onIce(data)
    },
    "1v1offer"(data) {
      //  offer
      this.onOffer(data)
    },
    "1v1hangup"(data) {
      // 
      this.clearState()
      this.$message({message: "~",type: "warning"})
      this.$store.dispatch('app/SET_ISTOCOARTBOARD', false)
      this.$eventBus.$emit('hangup') // webRTCStateapply
    }
  },
  async mounted() {
    this.conversation = this.currentconversation // Vue.dataconversationbug
    this.handlerResize()
    window.addEventListener('resize', this.handlerResize)
    if (this.state === 'apply') { // apply
      this.apply()
    } else if (this.state === 'reply') { // reply
      console.log('')
      await this.createP2P({...this.conversation, webRtcType: this.webRtcType}) //  peer  offer
      await this.onDataChannel() //  DataChannel
      console.log('')
      this.reply()
    }
  }
}
</script>

<style lang="scss">
.co-artboard {
  width: 100%;
  height: 100%;
  .co-artboard-main {
    width: 100%;
    height: 100%;
    .drawingarea {
      height: 100%;
      .select-list {
        display: flex;
        align-items: center;
        height: 45px;
        .select-item {
          margin-left: 10px;
          &:first-child {
            margin-left: 0;
          }
        }
      }
      .draw {
        height: calc(100% - 45px);
        canvas {
          border: 1px solid black;
        }
      }
    }
  }
}

</style>



