<template>
  <div
    class="audio-video"
    v-loading="loading"
    element-loading-spinner="el-icon-loading"
  >
    <div class="video-container" v-show="isToPeer && webRtcType === 'video'">
      <div>
        <video src id="rtcA" controls autoplay></video>
        <button @click="hangup">hangup</button>
      </div>
      <div>
        <video src id="rtcB" controls autoplay></video>
      </div>
    </div>
    <div class="audio-container" v-if="webRtcType === 'audio'">
      <el-tooltip class="item" effect="dark" content="" placement="top">
        <i class="icon el-icon-mic" @click="hangup"></i>
      </el-tooltip>
      <span class="secondary-font" style="display: block"></span>
    </div>
  </div>
</template>
<script>
export default {
  name: "CoVideo",
  props: ["currentconversation", "state", "webRtcType"],
  data() {
    return {
      conversation: {},
      loading: false,
      loadingText: "",
      isToPeer: false, //  P2P 
      peer: null,
      offerOption: {
        offerToReceiveAudio: 1,
        offerToReceiveVideo: 1
      }
    }
  },
  sockets: {
    async reply(data) {
      this.loading = false
      console.log(data)
      if (data.type && (data.type === 'disagree' || data.type === 'busy')) return
      //  peer
      await this.createP2P(data)
      //  offer
      this.createOffer(data)
    },
    // socket.on("joined", data => {
    //   this.userList = data
    // })
    // apply(data) {
    //   if (this.isCall) {
    //     this.reply(data.self, "3")
    //     return
    //   }
    //   this.$confirm(data.self + " , ?", "", {
    //     confirmButtonText: "",
    //     cancelButtonText: "",
    //     type: "warning"
    //   })
    //     .then(async () => {
    //       await this.createP2P(data) //  peer  offer
    //       this.isCall = data.self
    //       this.reply(data.self, "1")
    //     })
    //     .catch(() => {
    //       this.reply(data.self, "2")
    //     })
    // },
    '1v1answer'(data) {
      this.onAnswer(data)
    },
    '1v1offer'(data) {
      this.onOffer(data)
    },
    '1v1ICE'(data) {
      this.onIce(data)
    },
    '1v1hangup'(data) {
      this.localstream.getTracks().forEach(function (track) {
        track.stop()
      })
      this.$message({message: "~", type: "warning"})
      this.peer.close()
      this.peer = null
      this.isToPeer = false
      this.$store.dispatch('app/SET_IS_VIDEOING', false)
      this.$store.dispatch('app/SET_IS_AUDIOING', false)
      this.$eventBus.$emit('hangup') // webRTCStateapply
    }
  },
  methods: {
    hangup() {
      // 

      // 
      this.localstream.getTracks().forEach(function (track) {
        track.stop()
      })
      this.$message({message: "~", type: "warning"})
      this.$socket.emit("1v1hangup", {...this.conversation, webRtcType: this.webRtcType})
      this.peer.close()
      this.peer = null
      this.isToPeer = false
      this.$store.dispatch('app/SET_IS_VIDEOING', false)
      this.$store.dispatch('app/SET_IS_AUDIOING', false)
      this.$eventBus.$emit('hangup') // webRTCStateapply
    },
    apply() {
      this.loading = true
      this.loadingText = ""
      this.$socket.emit("apply", {...this.conversation, webRtcType: this.webRtcType})
    },
    reply() {
      this.$socket.emit("reply", {...this.conversation, webRtcType: this.webRtcType})
    },
    async createP2P(data) {
      this.loading = true
      this.loadingText = ""
      await this.createMedia(data)
    },
    async createMedia(data) {
      // 
      try {
        const webRtcType = this.webRtcType
        let constraints  = null
        if (webRtcType === 'video') {
          constraints = {
            audio: true,
            video: true
          }
        } else {
          constraints = {
            audio: true,
            video: false
          }
        }
        this.localstream = await navigator.mediaDevices.getUserMedia(constraints)
        let video = document.querySelector("#rtcA")
        video.srcObject = this.localstream
      } catch (e) {
        console.log("getUserMedia: ", e)
      }
      console.log('')
      this.initPeer(data) //  RTCPeerConnection
    },
    initPeer(data) {
      //  PeerConnection
      console.log(' PeerConnection')
      let PeerConnection =
        window.RTCPeerConnection ||
        window.mozRTCPeerConnection ||
        window.webkitRTCPeerConnection
      this.peer = new PeerConnection()
      console.log('localstreamlocalstreamlocalstream', this.localstream)
      this.peer.addStream(this.localstream) // 
      // ICE 
      this.peer.onicecandidate = event => {
        if (event.candidate) {
          this.$socket.emit("1v1ICE", {
            ...data,
            sdp: event.candidate
          })
        }
      }
      this.peer.onaddstream = event => {
        //  rtcB  src
        this.isToPeer = true
        this.loading = false
        let video = document.querySelector("#rtcB")
        video.srcObject = event.stream
      }
    },
    async createOffer(data) {
      //  offer
      try {
        // offer
        let offer = await this.peer.createOffer(this.offerOption)
        //  offer 
        await this.peer.setLocalDescription(offer)
        //  offer
        this.$socket.emit("1v1offer", {
          ...data,
          sdp: offer
        })
      } catch (e) {
        console.log("createOffer: ", e)
      }
    },
    async onOffer(data) {
      // offer answer
      try {
        //  offer 
        await this.peer.setRemoteDescription(data.sdp)
        //  answer
        let answer = await this.peer.createAnswer()
        //  answer 
        await this.peer.setLocalDescription(answer)
        //  answer
        this.$socket.emit("1v1answer", {
          ...data,
          sdp: answer
        })
      } catch (e) {
        console.log("onOffer: ", e)
      }
    },
    async onAnswer(data) {
      // answer
      try {
        await this.peer.setRemoteDescription(data.sdp) //  answer 
      } catch (e) {
        console.log("onAnswer: ", e)
      }
    },
    async onIce(data) {
      //  ICE 
      try {
        await this.peer.addIceCandidate(data.sdp) //  ICE
      } catch (e) {
        console.log("onAnswer: ", e)
      }
    }
  },
  async mounted() {
    this.conversation = this.currentconversation
    if (this.state === 'apply') { // apply
      this.apply()
    } else if (this.state === 'reply') { // reply
      console.log('')
      await this.createP2P({...this.conversation, webRtcType: this.webRtcType}) //  peer  offer
      console.log('')
      this.reply()
    }
  }
}
</script>
<style lang="scss" scoped>
.audio-video {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
}
.audio-container {
  text-align: center;
  .icon {
    font-size: 30px;
    color: hsla(201, 100%, 55%, 1);
    margin-bottom: 5px;
  }
}
.video-container {
  display: flex;
  justify-content: center;
  video {
    width: 400px;
    height: 300px;
    margin-left: 20px;
    background-color: #ddd;
  }
}
</style>


